.offers{
    width: 65%;
    height: 60vh;
    display: flex;
    margin: auto;
    padding: 0px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg, rgb(228, 206, 209) 0%, rgb(75, 53, 53)80%)
}
.offers-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;    
}
.offers-left h1{
    color: rgb(92, 77, 77);
    font-size: 80px;
    font-weight: 600;
}
.offers-left p{
    color: rgb(255, 255, 255);
    font-size: 22px;
    font-weight: 600;
}
.offers-left button{
    width: 282px;
    height: 70px;
    border-radius: 35px;
    background: red;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}
.offers-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 50px;
}